 /* –û—Å–Ω–æ–≤–Ω—ã–µ —Å—Ç–∏–ª–∏ */
 body {
   font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
   background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   min-height: 100vh;
   margin: 0;
   padding: 20px;
   color: #333;
 }

 /* –¢–µ–∫—Å—Ç –ø–æ–¥ –∑–∞–≥–æ–ª–æ–≤–∫–æ–º */
 body>div:not(.titleContainer):not(.buttonContainer) {
   font-size: 1.2rem;
   margin-bottom: 40px;
   opacity: 0;
   animation: fadeIn 1s ease-out 0.3s forwards;
   color: #555;
   text-align: center;
   line-height: 1.6;
 }

 /* –ê–¥–∞–ø—Ç–∏–≤–Ω–æ—Å—Ç—å */
 @media (max-width: 768px) {
   .titleContainer div {
     font-size: 2.2rem;
   }

   body>div:not(.titleContainer):not(.buttonContainer) {
     font-size: 1rem;
     padding: 0 15px;
   }

   .inputButton {
     padding: 12px 24px;
     font-size: 14px;
   }
 }

 @media (max-width: 600px) {
   .projects {
     max-width: 100%;
     padding: 0 10px;
   }

   select {
     padding: 10px 40px 10px 12px;
     font-size: 14px;
   }

   label {
     font-size: 15px;
   }
 }